@include desktop{
    .contenedor-productos{
        @include grid(4,2rem);
    }
    .filter-bar{
        display: flex;
        align-items: center;
        gap: 2rem;
       
    }
}

@include telefono{
    .contenedor-productos{
        @include grid(2,2rem);
    }

}
.contenedor-productos{
    margin-top: 5rem;
}
.producto{
    margin-bottom: 0;
    padding-bottom: 0;
}


.filter-bar{
    p{
        color: $grisClaro;
        font-weight: 100;
    }
}
.producto{
    border-radius: 5%;
    background-color: $blanco;
    margin-bottom: 3rem;

.contenedor-img-producto{

}
    .contenido-producto{
        padding: 0.5rem;
        
    }

    h3,a{
        color: $negro;
        margin: 0;
        font-size: 2rem;
        text-align: left;
        
    } & :hover{
        text-decoration: underline;
        cursor: pointer;
        
    }
    span{
        font-family: $fuente_nav;
        text-transform: uppercase;
    }
}

.precio{
        color: lighten($grisOscuro, 30%);
        font-weight: 100;
        font-size: 2rem;
}
    
